.filter{
    margin-top: 60px;
}

.filter .is-view{
    height: 420px;
    transition: all .5s;
    overflow: hidden;
}

.filter .filter-show{
    height: 0 !important;
}

.filter button{
    margin-top: 45px;
    height: 38px;
    width: 93px;
    margin-right: 20px;
    background: none;
    outline: none;
    font-family: Roboto, sans-serif;
    font-size: 12px;
    font-weight: 700;
    border: 1px solid black;
    color: black;
    cursor: pointer;
    transition-property: all;
    transition-duration: .4s;
}

.filter button:hover{
    background-color: #f0685a;
    color: white;
    border: inherit;
}

.filter .filter-toggle p{
    font-family: Roboto, sans-serif;
    font-size: 14px;
    font-weight: 700;
    text-transform: uppercase;
    cursor: pointer;
}

@media(max-width: 1024px){

    .filter{
        margin-top: 25px;
    }

    .filter .is-view{
        height: 0;
        transition: all .5s;
        overflow: hidden;
    }
    
    .filter .filter-show{
        height: 415px !important;
    }
}