.stock{
    margin: 0 auto;
    background-color: #faf6f6;
    padding-top: 30px;
    padding-bottom: 80px;
    font-family: 'Roboto', sans-serif;
}

.stock-block{
    max-width: 1170px;
    margin: 0 auto;
}

.stock h1{
    text-align: center;
    padding-bottom: 30px;
    font-size: 18px;
    text-transform: uppercase;
    letter-spacing: 2px;
}

.flex{
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
}

@media(max-width: 991.98px){
    .stock{
        padding-bottom: 20px;
    }
}