.sorting .sort-toggle{
    height: 0 !important;
}

.sorting .items-sort{
    display: none;
    background-color: white;
    width: 100%;
    height: 70px;
    transition: all .5s;
    overflow: hidden;
}

.sorting .item-sort{
    font-family: Roboto, sans-serif;
    font-weight: 700;
    font-size: 12px;
    color: #6b6868;
    padding: 10px 0;
    cursor: pointer;
}

.sorting .item-sort:hover{
    color: #f0685a;
}


.sorting .main-sort-row{
    font-family: Roboto, sans-serif;
    font-weight: 700;
    font-size: 14px;
    cursor: pointer;
}

@media(max-width: 1024px){

    .sorting>div{
        width: 100%;
    }

    .sorting .sort-toggle{
        height: 70px !important;
    }

    .sorting .items-sort{
        height: 0px;
    }
}