.filter-list{
    margin-top: 50px;
}

.filter-list .filter-header p{
    font-family: Roboto, sans-serif;
    font-size: 14px;
    font-weight: 700;
}

.filter-list .filter-block-items{
    overflow: hidden;
    transition: all 1s;
}

.filter-list .filter-block-items.hide{
    height: 0;
    opacity: 0;
}