.total-order>div{
    display: flex;
    padding-top: 20px;
    padding-bottom: 20px;
    border-bottom: 1px solid #cacaca;
}

.total-order>div p{
    font-family: Roboto, sans-serif;
    font-size: 16px;
    font-weight: 700;
}

.total-order__price{
    width: 145px;
}

.total-order__text{
    width: 175px;
    margin-left: auto;
}

.total-order>a{
    display: block;
    width: 100%;
    text-align: right;
    padding-bottom: 140px;
}

.total-order button{
    margin-top: 42px;
    margin-left: auto;
    border: none;
    outline: none;
    background: none;
    background-color: #f0685a;
    min-height: 38px;
    width: 182px;
    font-family: Roboto, sans-serif;
    color: white;
    font-size: 10px;
    transition: .1s;
}

.total-order button:hover{
    color: white;
    background-color: rgba(140, 22, 50, 1);
    border: none;
}

@media(max-width: 750px){
    .total-order button{
        margin-right: 15px;
    }
}