.detail__box{
    max-width: 1200px;
    margin: 0 auto 160px;
}

.detail .detail-image{
    width: 100%;
}

.detail .detail-image img{
    width: 100%;
}

@media(max-width:750px){
	.detail__box{
        margin-bottom: 10px;
    }

    .detail .detail-image{
        display: none;
    }
}