.stock-item{
    text-align: center;
    background-color: #f0685a;
    width: 200px;
    min-height: 200px;
}

.stock-item div{
    font-family: 'Roboto', sans-serif;
    color: white;
}

.stock-item>div:first-child{
    font-weight: normal;
    font-size: 18px;
    padding: 25px 10px 0;
}

.stock-item>div:last-child{
    font-size: 14px;
    padding: 25px;
}

@media(min-width: 576px) and (max-width: 991.98px){
    .stock-item{
        width: 49%;
        margin-top: 5px;
    }
}

@media(max-width: 575px){
    .stock-item{
        width: 98%;
        margin-top: 5px;
        min-height: 150px;
    }
}