.footer{
    background-color: #484647;
    margin-top: 15px;
}

.footer__container{
    display: flex;
    max-width: 1170px;
    margin: 0 auto;
    display: flex;
    padding: 50px 10px 30px;
}

.footer__contact-block{
    width: 20%;
    display: flex;
    flex-flow: column;
    justify-content: space-between;
}

.footer__social-block{
    width: 20%;
    display: flex;
    flex-flow: column;
    justify-content: space-between;
}

.footer__logo-block{
    width: 80%;
    display: flex;
    min-height: 150px;
    position: relative;
}

.footer__address{
    color: white;
    font-size: 14px;
}

.footer__phone-mail p{
    color: white;
    font-size: 14px;
}

.footer__logo{
    color: white;
    font-size: 36px;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);    
}

.footer__social-text{
    color: white;
    font-size: 14px;
}

.footer__social-web{
    display: flex;
}

.footer__social-web div{
    width: 20px;
    margin-right: 25px;
    fill: #fff;
}

.footer__copyright{
    max-width: 1170px;
    margin: 0 auto;
}

.footer__copyright p{
    font-size: 12px;
    text-align: center;
    color: #a3a3a3;
    padding: 25px;
}

@media(max-width: 1040px){
    .footer__container{
        flex-direction: column;
        padding-top: 0;
        padding-bottom: 0;
    }

    .footer__contact-block, .footer__social-block, .footer__logo-block{
        width: 100%;
        text-align: center;
        margin-bottom: 15px;
    }

    .footer__logo-block{
        order: -1;
        min-height: 60px;
        margin-bottom: 0;
    }

    .footer__logo{
        font-size: 28px;
    }

    .footer__contact-block p{
        margin-bottom: 5px;
        font-size: 12px;
    }

    .footer__social-web{
        margin: 0 auto;
        font-size: 12px;
    }

    .footer__social-web div{
        margin: 15px 12px 0;
        width: 20px;
    }

    p.footer__social-text{
        display: none;
    }

    .footer__copyright p{
        padding: 15px;
        font-size: 10px;
    }
}