.filter-list-item{
    padding-top: 25px;
}

.filter-list-item .item-text p{
    font-family: Roboto, sans-serif;
    font-size: 12px;
    font-weight: 700;
    color: #6b6868;
    transition-property: all;
    transition-duration: .1s;
    cursor: pointer;
}

.filter-list-item .item-text p.active{
    color: #f0685a;
}

.filter-list-item .item-text p:hover{
    color: #f0685a;
}