.tea-list-item {
    text-align: center;
    padding: 5px; 
    margin-top: 35px; 
    margin-bottom: 45px;    
    padding-bottom: 20px;
}

.tea-list-item *{
    margin: 0;  
}

.tea-list-item img{
    max-width: 216px;
}

.tea-list-item button{
    margin-top: 30px;
    background-color: white;
    border: none;
    font-size: 14px;
    font-family: 'Roboto', sans-serif;
    transition: .5s;
    font-weight: 700;
}

.tea-list-item button+span{
    content: '';
    width: 41px;
    height: 3px;
    background-color: white;
    display: block;
    margin: 2px auto;
    transition: .8s;
}

.tea-list-item button:focus{
    outline: none;
}

.tea-list-item button:hover{
    color: red;
}

.tea-list-item button:hover+span{
    background-color: red;
}

.tea-list-item .tea-name {
    font-family: 'Roboto', sans-serif;
    font-size: 18px;
    font-weight: 700;
    color: rgb(29, 29, 29);
    margin-top: 35px;
}

.tea-list-item .tea-category {
    font-size: 14px;
    color: #737171;
    margin-top: 7px;
    font-weight: 300;
}

.tea-list-item .tea-price {
    font-weight: 300;
    font-size: 14px;
    margin-top: 20px;
}

button.tttt{
    color:#fc0303;
}

button.tttt:hover{
    color: #fc0303;
}

@media(max-width: 576px){
    .tea-list-item .tea-name{
        margin-top: 10px;
    }
}