.basket-item{
    display: flex;
    border-bottom: 1px solid #cacaca;
    padding: 20px;
}

.basket-item .photo-name{
    display: flex;
    align-items: center;    
}

.basket-item .image-x{
    width: 150px;
}

.basket-item .image-x img{
    width: 100%;
}

.basket-item .title-block{
    margin-left: 25px;  
}

.basket-item .bi-title{
    font-family: Roboto, sans-serif;
    font-size: 18px;
    font-weight: 700;
}

.basket-item .bi-catcon{
    font-family: Roboto, sans-serif;
    font-size: 12px;
    color: #8f8c8c;
    margin-top: 3px;
    font-weight: 700;
}

.basket-item .price-count{
    display: flex;
    align-items: center;
    margin-left: auto;
    font-family: Roboto, sans-serif;
    font-size: 14px;
}

.basket-item .price{
    width: 100px;
}

.basket-item .buttons{
    width: 175px;
}

.basket-item .inc-dec{
    display: flex;
    align-items: center;
    border: 1px solid black;
    width: 130px;
    height: 45px;
}

.basket-item .m, .basket-item .p{
    width: 35px;
    text-align: center;
    cursor: pointer;
    user-select: none;
}

.basket-item .value{
    text-align: center;
    flex-grow: 1;
}

.basket-item .sum{
    width: 100px;
}

.basket-item .del{
    width: 25px;
}

@media(max-width: 850px){

    .basket-item .price{
        display: none;
    }
}

@media(max-width: 735px){
    .basket-item{
        flex-direction: column;
    }

    .basket-item .image-x{
        width: 100px;
    }

    .basket-item .price-count{
        margin-top: 10px;
        margin-left: 0;
    }

    .basket-item .inc-dec{
        height: 35px;
    }
}