.basket-page{
    max-width: 1170px;;
    margin: 0 auto;
}

.basket-page .basket-header{
    font-family: Roboto, sans-serif;
    font-size: 18px;
    text-align: center;
    margin-top: 50px;
    margin-bottom: 30px;
    font-weight: 700;
    text-transform: uppercase;
    letter-spacing: 3px;
}

.basket-flex-header{
    display: flex;
    margin-left: auto;
    border-bottom: 1px solid #cacaca;
    padding-bottom: 15px;
}

.basket-flex-header p{
    color: #8f8c8c;
    font-size: 14px;
}

.basket-flex-header__price{
    margin-left: auto;
    width: 100px;
}

.basket-flex-header__count{
    width: 175px;
}

.basket-flex-header__sum{
    width: 145px;
}

@media(max-width: 1170px){

    .basket-page .flex-basket-item, .basket-page .flex-total-price, .basket-flex-header{
        width: 98%;
        margin: 0 auto;
    }
}

@media(max-width: 850px){

    .basket-flex-header__price{
        display: none;
    }
    
    .basket-flex-header__count{
        margin-left: auto;
    }
}

@media(max-width: 735px){
    .basket-flex-header{
        display: none;
    }
}