.header{
    min-height: 80px;
    position: relative;
}

.header .logo{
    font-size: 28px;
    font-weight: bold;
    position: absolute;
    top: 50%;
    left: 150px;
    transform: translateY(-50%);   
}

.header .nav-icon{
    width: 20px;
    height: 20px;
    position: absolute;
    top: 50%;
    left: 25px;
    transform: translateY(-50%);
    display: none;
}

.header ul{
    display: flex;
    justify-content: space-between;
    text-transform: uppercase;
    font-size: 12px;
    font-weight: bold;
    position: absolute;
    top: 50%;
    right: 240px;
    transform: translateY(-50%); 
}

.header ul li{
    margin-left: 35px;
    position: relative;
    color: pink;
    transition-property: color;
    transition-duration: .5s;
}

.header ul li span{
    display: block;
    height: 1px;
    width: 80%;
    opacity: 0;
    background-color: red;
    position: absolute;
    bottom: -5px;
    left: 50%;
    transform: translateX(-50%);
    transition-property: opacity;
    transition-duration: .5s;
}

.header ul li a{
    transition-property: color;
    transition-duration: .3s;
}

.header ul li:hover span{
    opacity: 1;
}

.header ul li:hover a{
    color: red;
}

.header ul li:active span{
    opacity: 1;
}

.header .basket{
    width: 20px;
    position: absolute;
    top: 50%;
    right: 150px;
    transform: translateY(-50%);
}

@media(max-width: 865px){

   
    .header .logo{
        font-size: 28px;
        font-weight: bold;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);   
    }

    .header ul{
        width: 0px;
        min-height: 100vh;
        display: block;
        margin: 0;
        overflow: hidden;
        transition: .1s;
        transition-timing-function: linear;
        position: absolute;
        top: 0;
        left: 0;
        padding-top: 50px;
        transform: translateY(0);
        background-color: white;
        z-index: 500;
        text-align: center;
        overflow: auto;
    }

    .header ul li{
        padding: 15px;
        margin-left: 0;
    }

    .header ul.nav-show{
        width: 100%;
        position: fixed;
    }

    .header .nav-icon{
        display: block;
        z-index: 1000;
    }

    .header .basket{
        right: 15px;
    }

    html.no-scroll{
        overflow: hidden;
    }

}