.detail-main{
    display: flex;
	flex-wrap: wrap;
    margin: 0 auto;
}

.detail-main .image{
    box-sizing: border-box;
    width: 50%;
    padding: 130px 25px 0;
}

.detail-main .image img{
    width: 90%;
    max-width: 400px;
    margin: 0 auto;
    display: block;
}

.detail-main .discription{
    box-sizing: border-box;
    width: 50%;
    margin-top: 130px;
    padding: 0 25px;
}

.detail-main .title{
    font-size: 28px;
    font-family: 'Roboto', sans-serif;
    font-weight: 700;
}

.detail-main .category, .detail-main .country{
    font-family: 'Roboto', sans-serif;
    color: #737272;
    font-size: 12px;
    font-weight: 700;
}

.detail-main .category{
    margin-top: 15px;
}

.detail-main .country{
    margin-top: 8px;
}

.detail-main .discription-text{
    font-family: 'Roboto', sans-serif;
    font-size: 14px;
    line-height: 24px;
    margin-top: 45px;
}

.detail-main .price{
    font-family: 'Roboto', sans-serif;
    font-size: 14px;
    font-weight: 700;
    margin-top: 25px;
}

.detail-main .add-tea-block{
    display: flex;
    margin-top: 35px;
}

.detail-main .inc-dec{
    border: 1px solid black;
    display: flex;
    width: 82px;
    height: 38px;
	user-select: none;
}

.detail-main .minus, .detail-main .plus{
    width: 20px;
    display: flex;
    font-size: 10px;
}

.detail-main .minus p, .detail-main .plus p{
    font-weight: 700;
}

.detail-main .massa{
    display: flex;
    flex-grow: 1;
}

.detail-main .inc-dec p{
    margin: auto;
    font-size: 10px;
}

.detail-main button{
    width: 135px;
    height: 38px;
    background-color: #f0685a;
    color: white;
    border: none;
    font-family: 'Roboto', sans-serif;
    font-size: 10px;
    margin-left: 23px;
    transition: .1s;
}

.detail-main button.tttt{
    background-color: rgba(23, 64, 29, .7);
    color: white;
    border: none;
}

.detail-main button.tttt:hover{
    background-color: rgba(23, 64, 29, .7);
    color: white;
}

.detail-main button:focus{
    outline: none;
}

.detail-main button:hover{
    color: white;
    background-color: rgba(140, 22, 50, 1);
    border: none;
}

@media(max-width:750px){
	.detail-main .discription{
        margin-top: 0;
        width: 100%;
        padding: 10px;
    }
    
    .detail-main .image{
        margin-top: 0;
        margin-left: auto;
        margin-right: auto;
        width: 70%;
        padding: 10px;
    }
}