.catalog .flex{
    display: flex;
    flex-wrap: wrap;
    max-width: 1170px;
    margin: 0 auto;
}

.catalog .catalog-image{
    width: 100%;
    height: 250px;
    overflow: hidden;
}

.catalog .catalog-image img{
    width: 100%;
}

.catalog .flex>div:first-child{
    width: 30%;
    margin-top: 50px;
}

.catalog .flex-list{
    width: 65%;
}

@media(max-width: 1024px){

    .catalog .flex-list{
        width: 90%;
        margin: auto;
    }

    .catalog .flex>div:first-child{
        width: 100%;
        margin-top: 15px;
    }
}

@media(max-width: 575px){
    .catalog .flex>div:first-child{
        padding: 0 10px;
    }

    .catalog .catalog-image{
        display: none;
    }
}

@media(min-width: 576px) and (max-width: 1024px){
    .catalog .catalog-image{
        height: 150px;
    }
}

@media(max-width: 1024px){
    .catalog .flex>div:first-child{
        margin-top: 10px;
        padding: 0 25px;
    }
}