.contacts{
    margin: 0 auto 160px;
    text-align: center;
    font-family: 'Roboto', sans-serif;
}

.contacts .detail-image img{
    width: 100%;
}

.contacts__header{
    margin-top: 85px;
    font-size: 18px;
    font-family: 'Roboto', sans-serif;
    text-transform: uppercase;
    letter-spacing: 2px;
    font-weight: 700;
    margin-bottom: 100px;
}

.contacts__text{
    font-size: 14px;
    font-family: 'Roboto', sans-serif;
    margin-top: 30px;
}

.contacts__contacts{
    font-size: 14px;
    font-family: 'Roboto', sans-serif;
    margin-top: 45px;
    font-weight: 700;
}