.tea-list{
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
}

.tea-list .card{
    width: 31%;
}

@media(min-width: 576px) and (max-width:991.98px){
    .tea-list .card{
        width: 48%;

    }
}

@media(max-width: 575.98px){
    .tea-list .card{
        width: 100%;
    }
}