.most-popular{
    max-width: 1170px;
    margin: 0 auto;
}

.most-popular h1{
    text-align: center;
    font-family: 'Roboto', sans-serif;
    font-size: 18px;
    font-weight: 700;
    padding-top: 50px;
    text-transform: uppercase;
    letter-spacing: 2px;
}

.most-popular .flex{
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
}

.most-popular .flex div{
    width: 24%;
}

@media(min-width: 576px) and (max-width: 991.98px){
    .most-popular .flex div{
        width: 49%;
        margin-top: 10px;
    }
}

@media(max-width:575.98px){
    .most-popular .flex div{
        width: 80%;
    }
}