.slider-block{
    position: relative;
}

.slider{
    width: 100%;

}

.slider div{
    width: 100%;
}

.slider img{
    width: 100%;
}

.slider-block .slick-arrow{
    width: 50px;
    height: 50px;
    padding: 10px;
    border: none;
    background-color: rgba(255, 255, 255, .4);
    position: absolute;
    z-index: 10;
    top: 50%; 
    transform: translateY(-25px);
}

.slider-block .slick-arrow:hover{
    background-color: rgba(255, 255, 255, 1);
}

.slider-block .slick-arrow:focus{
    outline: none;
}

.slider-block .next-arrow{
    right: 20px;
}

.slider-block .prev-arrow{
    left: 20px;
}

@media(max-width: 750px){

    .slider-block .slick-arrow{
        width: 26px;
        height: 26px;
        padding: 5px;
        transform: translateY(-13px);
    }

    .slider-block .next-arrow{
        right: 10px;
    }
    
    .slider-block .prev-arrow{
        left: 10px;
    }
}