*{
	margin: 0;
    padding: 0;
    box-sizing: border-box;
	list-style: none;
	text-decoration: none;
	font-family: 'Roboto', sans-serif;
	color: black;
}

body{
	background-color:#f3f3f3;
	height: 100%;
}

html{
	height: 100%;
}

.app{
	max-width: 1440px;
	margin: 0 auto;
	background-color: white;
	min-height: 100%;
	display: flex;
	flex-direction: column;
}

#root{
	height: 100%;
}

header, footer{
	flex-grow: 0;
}

.main-block{
	flex-grow: 1;
}